"use client";

import Image from "next/image";

interface HuboHeadingProps {
  text: string;
}

export default function HuboHeading({ text }: HuboHeadingProps) {
  return (
    <section className="w-[90vw] bg-transparent text-white py-10 flex flex-col md:flex-row items-center justify-between gap-10 mx-auto">
      
      {/* Left: Heading */}
      <div className="w-full md:w-1/2 px-4 space-y-2 text-left">
        <h1 className="text-[50px] md:text-[50px] font-extrabold leading-tight">
          {text}
        </h1>
      </div>

      {/* Right: Large image with 2 smaller overlapping images */}
      <div className="w-full md:w-1/2 flex items-center justify-center relative">
        {/* Main Large Image */}
        <div className="relative w-80 h-96 rounded-xl overflow-hidden shadow-lg">
          <Image
            src="/img/ai.jpeg"
            alt="Main Image"
            fill
            className="object-cover"
          />
        </div>

        {/* Top-right smaller image */}
        <div className="absolute top-[-20px] right-[-20px] w-32 h-32 rounded-lg overflow-hidden shadow-xl border-4 border-white">
          <Image
            src="/img/ai1.jpg"
            alt="Overlay Top Right"
            fill
            className="object-cover"
          />
        </div>

        {/* Bottom-left smaller image */}
        <div className="absolute bottom-[-20px] left-[-20px] w-28 h-28 rounded-lg overflow-hidden shadow-xl border-4 border-white">
          <Image
            src="/img/ai2.jpg"
            alt="Overlay Bottom Left"
            fill
            className="object-cover"
          />
        </div>
      </div>
    </section>
  );
}
